	                  // copy and related and append the result to path.
	                  path += issuerCopy.getId(related);
	
	                  // 5.4.5.3) Append <, the hash in result, and > to path.
	                  path += '<' + result.hash + '>';
	
	                  // 5.4.5.4) Set issuer copy to the identifier issuer in
	                  // result.
